'use client'

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'

export default function DownloadPage() {
  const [isDownloading, setIsDownloading] = useState(false)
  const [downloadError, setDownloadError] = useState('')

  const handleDownload = async () => {
    try {
      setIsDownloading(true)
      setDownloadError('')
      
      // Create a link element and trigger download
      const link = document.createElement('a')
      link.href = '/confidence-journal.pdf'
      link.download = 'HealTale-Confidence-Journal.pdf'
      document.body.appendChild(link)
      link.click()
      document.body.removeChild(link)
      
      // Simulate download delay
      setTimeout(() => {
        setIsDownloading(false)
      }, 1000)
      
    } catch (error) {
      setDownloadError('Download failed. Please try again.')
      setIsDownloading(false)
    }
  }

  const journalFeatures = [
    {
      title: "Daily Affirmations",
      description: "Science-backed positive statements to rewire negative self-talk",
      icon: "💝"
    },
    {
      title: "Mini Science Facts",
      description: "Did you know your skin has its own ecosystem? Learn fascinating facts daily",
      icon: "🔬"
    },
    {
      title: "Guided Reflections",
      description: "Thoughtful prompts like 'What makes me feel good that has nothing to do with how I look?'",
      icon: "📝"
    },
    {
      title: "Myth-Busting Pages",
      description: "Quick reference to debunk beauty myths with real science",
      icon: "🎯"
    },
    {
      title: "Progress Tracking",
      description: "Monitor your confidence journey with weekly check-ins",
      icon: "📈"
    },
    {
      title: "Emergency Confidence Kit",
      description: "Quick exercises for moments when self-doubt creeps in",
      icon: "🆘"
    }
  ]

  return (
    <div className="min-h-screen py-12 px-4 sm:px-6 lg:px-8">
      <div className="max-w-4xl mx-auto">
        {/* Header Section */}
        <div className="text-center mb-16">
          <h1 className="text-4xl sm:text-5xl font-bold text-gray-800 mb-6">
            The Confidence Journal
          </h1>
          <p className="text-xl text-gray-600 max-w-3xl mx-auto leading-relaxed">
            Your personal guide to building science-backed confidence. This printable workbook 
            combines daily affirmations, fascinating science facts, and guided reflections to 
            help you develop a healthy relationship with yourself.
          </p>
        </div>

        {/* Preview Section */}
        <div className="mb-12">
          <Card className="bg-gradient-to-br from-purple-50 to-rose-50 border-purple-100 overflow-hidden">
            <CardHeader className="text-center pb-4">
              <CardTitle className="text-2xl text-gray-800 mb-2">
                What's Inside Your Journal
              </CardTitle>
              <Badge variant="secondary" className="bg-purple-100 text-purple-700 mx-auto">
                40+ Pages of Science-Backed Content
              </Badge>
            </CardHeader>
            <CardContent>
              <div className="grid md:grid-cols-2 gap-6">
                {journalFeatures.map((feature, index) => (
                  <div key={index} className="flex items-start gap-4 p-4 bg-white/60 rounded-lg">
                    <div className="text-2xl">{feature.icon}</div>
                    <div>
                      <h3 className="font-semibold text-gray-800 mb-1">{feature.title}</h3>
                      <p className="text-sm text-gray-600">{feature.description}</p>
                    </div>
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>
        </div>

        {/* Sample Content Preview */}
        <div className="mb-12">
          <h2 className="text-2xl font-bold text-gray-800 mb-6 text-center">
            Sample Pages Preview
          </h2>
          <div className="grid md:grid-cols-2 gap-8">
            <Card className="bg-white/80 border-purple-100">
              <CardHeader>
                <CardTitle className="text-lg text-purple-600">Daily Affirmation Example</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="bg-purple-50 p-4 rounded-lg italic text-gray-700">
                  <p className="mb-2">"My worth is not determined by my appearance."</p>
                  <p className="text-sm text-gray-600">
                    <strong>Science fact:</strong> Self-worth activates the same brain regions 
                    as physical warmth, creating genuine feelings of comfort and security.
                  </p>
                </div>
              </CardContent>
            </Card>

            <Card className="bg-white/80 border-purple-100">
              <CardHeader>
                <CardTitle className="text-lg text-purple-600">Reflection Prompt Example</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="bg-rose-50 p-4 rounded-lg">
                  <p className="font-medium text-gray-800 mb-2">
                    "What makes me feel confident that has nothing to do with how I look?"
                  </p>
                  <div className="space-y-2 text-sm text-gray-600">
                    <p>• My problem-solving skills</p>
                    <p>• How I make my friends laugh</p>
                    <p>• My curiosity about science</p>
                    <p>• _________________</p>
                  </div>
                </div>
              </CardContent>
            </Card>
          </div>
        </div>

        {/* Download Section */}
        <div className="text-center">
          <Card className="bg-gradient-to-r from-purple-100 to-rose-100 border-purple-200 max-w-2xl mx-auto">
            <CardContent className="p-8">
              <h2 className="text-2xl font-bold text-gray-800 mb-4">
                Ready to Start Your Confidence Journey?
              </h2>
              <p className="text-gray-600 mb-6">
                Download your free Confidence Journal and begin building a healthier 
                relationship with yourself through science and self-compassion.
              </p>
              
              <Button 
                onClick={handleDownload}
                disabled={isDownloading}
                className="bg-gradient-to-r from-purple-600 to-rose-600 hover:from-purple-700 hover:to-rose-700 text-white px-8 py-4 text-lg rounded-full transition-all duration-300 transform hover:scale-105 disabled:transform-none disabled:opacity-70"
              >
                {isDownloading ? (
                  <>
                    <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white mr-2"></div>
                    Downloading...
                  </>
                ) : (
                  <>
                    📥 Download Your Journal (PDF)
                  </>
                )}
              </Button>
              
              {downloadError && (
                <p className="text-red-600 mt-4 text-sm">{downloadError}</p>
              )}
              
              <p className="text-xs text-gray-500 mt-4">
                Free download • No email required • Print-friendly format
              </p>
            </CardContent>
          </Card>
        </div>

        {/* Additional Info */}
        <div className="mt-12 text-center">
          <div className="bg-white/60 rounded-lg p-6 max-w-2xl mx-auto">
            <h3 className="font-semibold text-gray-800 mb-3">How to Use Your Journal</h3>
            <div className="text-sm text-gray-600 space-y-2">
              <p>• Print on standard 8.5" x 11" paper for best results</p>
              <p>• Set aside 10-15 minutes each day for journaling</p>
              <p>• Use a pen or pencil - handwriting helps with retention</p>
              <p>• Be patient with yourself - confidence building takes time</p>
              <p>• Share insights with trusted friends or family if you'd like</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
